@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,400;1,300;1,400;1,900&family=Quicksand:wght@300;400;500;600;700&family=Roboto:ital,wght@0,300;0,400;1,900&display=swap');


.body-area {
  display: grid;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.create-section {
  margin-top: 25px;
}

.form-container {
  font-family: 'Roboto', sans-serif;
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
  background-color: var(--navyblue);
  border-radius: 0 0 5px 5px;
  border: 1px solid #3a3e59;

  -webkit-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
}

.title {
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-style: italic;

  padding: 10px;
  border-radius: 5px 5px 0 0px;
}

.title {
  background-color: #255784;
  color: #ed6b5b;
  user-select: none;

  -webkit-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
}

.message {
  color: white;
  user-select: none;

  -webkit-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
}

.red-message {
  background-color: var(--darkpink);
}

.green-message {
  background-color: rgb(94, 153, 7) !important;
}

.theory {
  display: grid;
}

.lab label,
.theory label {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 1.2rem;
  text-align: center;
  color: #ed6b5b;
  user-select: none;
}

.theory input {
  font-size: 1.1rem;
  padding: 0.4rem 0.2rem;
  user-select: none;
  border-radius: 5px;
  /* border: 1px solid rgb(185, 28, 0); */

  font-family: 'Quicksand', sans-serif;
  font-weight: 600;
  border: 1px solid #3a3e59;
  background-color: #c36b84;
}

.type {
  font-family: 'Quicksand', sans-serif;
  font-weight: 600;
  font-size: 1rem;
  padding: 0.2rem 0.2rem;
  user-select: none;
  cursor: pointer;
  border-radius: 5px;
  /* border: 1px solid rgb(185, 28, 0); */
  border: 1px solid #3a3e59;
  background-color: #c36b84;
}


#Sentence {
  user-select: none;
  height: 4.8rem;
  border-radius: 5px;
  border: 1px solid #3a3e59;
  background-color: #c36b84;
  font-size: 1.2rem;
  font-family: 'Quicksand', sans-serif;
  font-weight: 500;
}

.buttons,
.buttons button {
  display: flex;
  justify-content: space-between;
}

.w3-btn {
  font-size: 1.2rem;
  padding: 0.2rem 0.5rem;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border-color: #3a3e59;
  background-color: #255784;
  color: rgb(209, 207, 207);
  cursor: pointer;
}

input[type="text"],
textarea:focus {
  background-color: #c36b84;
}