#numpad {
    display: flex;
    flex-wrap: wrap;
    width: 250px;
}

#numpad button {
    flex: 1 1 33%;
    height: 50px;
    font-size: 2rem;
    background-color: #ddd;
    border: none;
    outline: none;
}

#numpad #backspace {
    background-color: #f44336;
    color: #fff;
}

.numpad-container #display {
    width: 250px;
    height: 50px;
    font-size: 2rem;
    text-align: right;
    padding: 0 10px;
    background-color: rgb(196, 192, 192);
}

#numpad button:hover {
    background-color: #ccc;
}

#numpad button:active {
    background-color: #aaa;
}

.numpad-container .history-list {
    height: 100px;
    overflow: scroll;
    background-color: #aaa;
    height: 4rem;
    font-size: 1.5rem;
}

.numpad-container #question {
    background-color: #aaa;
    height: 2rem;
    font-size: 1.5rem;
}


.numpad-container {
    display: grid;
    justify-content: center;
    align-items: center;
}