.single-multiplicand,
.single-multiplier {
  padding: 0.4rem 0.3rem;
}
.single-multiplicand button {
  padding: 0.2rem 0.5rem;
}
.single-multiplicand input {
  width: 4rem;
  padding: 0.2rem 0.5rem;
}
.single-multiplier button {
  padding: 0.2rem 0.5rem;
}
.single-multiplier input {
  width: 4rem;
  padding: 0.2rem 0.5rem;
}
.multiplication-table-settings-form {
  display: grid;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: var(--light-black);
}
.submit-btn {
  width: 5rem;
  padding: 0.3rem 0.4rem;
  margin: 0.3rem auto;
}
