@import url("https://fonts.googleapis.com/css2?family=Bangers&family=Lobster&family=Roboto+Mono:wght@500&family=Roboto:wght@500&family=Russo+One&family=Varta&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bangers&family=Lobster&family=Odibee+Sans&family=Roboto+Mono:wght@500&family=Roboto:wght@500&family=Russo+One&family=Varta&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,400;1,300;1,400;1,900&family=Quicksand:wght@300;400;500;600;700&family=Roboto:ital,wght@0,300;0,400;1,100;1,900&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
*::after,
*::before {
  box-sizing: border-box;
  /* border: 1px dashed black; */
  margin: 0;
  padding: 0;
}
:root {
  --blue: #255784;
  --darkpink: #c36b84;
  --orange: #ed6b5b;
  --navyblue: #3a3e59;
  --paste: #358486;
  --light-black: #282c34;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;
  height: 100vh;
  background-color: var(--navyblue);
}
