.output-table {
    border-collapse: collapse;
    width: 100%;
  }
  
  .output-table th, .output-table td {
    border: 1px solid #ddd;
    padding: 1.3px;
    text-align: left;
  }
  
 .output-table th {
    background-color: #f2f2f2;
  }
  .output-table{
    background-color: rgb(3, 85, 85);
  }